// В этом файле должны быть стили для БЭМ-блока card, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.card {
	background: var(--color-white);
	border-radius: 32px;
	padding: 24px;
	@media (min-width: $screen-sm) {
		padding: 32px;
	}
	@media (min-width: $screen-md) {
		padding: 48px 40px 40px;
	}
	&__content {
		display: flex;
		flex-direction: column;
		height: 100%;
	}
	&__label {
		font-family: var(--font-accent);
		font-size: 14px;
		font-weight: 700;
		letter-spacing: 0.03em;
		line-height: 120%;
		text-transform: uppercase;
		@media (min-width: $screen-sm) {
			font-size: 16px;
		}
	}
	&__header {
		font-family: var(--font-accent);
		font-size: 20px;
		font-weight: 700;
		line-height: 120%;
		margin: 4px 0 24px;
		@media (min-width: $screen-sm) {
			font-size: 24px;
		}
		@media (min-width: $screen-md) {
			font-size: 28px;
		}
	}
	&__subheader {
		color: var(--color-green);
		font-family: var(--font-accent);
		font-size: 24px;
		font-weight: 700;
		line-height: 120%;
		margin: 40px 0 8px;
	}
	&__table {
		display: grid;
		grid-gap: 16px;
		margin: 8px 0 24px;
	}
	&__table-row {
		box-shadow: inset 0px -1px 0px rgba(16, 193, 76, 0.25);
		display: flex;
		justify-content: space-between;
		padding: 0 0 4px;
	}
	&__table-title {
		font-size: 18px;
		line-height: 111%;
	}
	&__table-cell {
		color: var(--color-green);
		font-family: var(--font-accent);
		font-size: 32px;
		font-weight: 700;
		line-height: 100%;
	}
	&__p {
		font-size: 16px;
		line-height: 133%;
		@media (min-width: $screen-sm) {
			font-size: 20px;
		}
		&_small {
			font-size: 14px;
			line-height: 150%;
			margin-bottom: 16px;
			@media (min-width: $screen-sm) {
				font-size: 16px;
			}
		}
		span {
			font-weight: 700;
		}
		.red {
			color: var(--color-red);
		}
		.green {
			color: var(--color-green);
		}
	}
	&__text {
		font-size: 18px;
		line-height: 111%;
		margin: 64px 0 16px;
		text-align: center;
		span {
			color: var(--color-red);
			display: block;
			font-family: var(--font-accent);
			font-size: 28px;
			font-weight: 700;
			line-height: 100%;
			margin-top: 4px;
			@media (min-width: $screen-sm) {
				font-size: 32px;
			}
		}
	}
	&__small {
		font-size: 12px;
		line-height: 100%;
		margin: auto 0 8px;
		text-align: center;
		@media (min-width: $screen-sm) {
			font-size: 16px;
		}
	}
	&__list {
		list-style: disc outside;
		padding: 0 0 0 30px;
		margin: 16px 0 auto;
	}
	&__li {
		font-size: 16px;
		line-height: 120%;
		@media (min-width: $screen-sm) {
			font-size: 20px;
		}
		& + & {
			margin-top: 8px;
		}
	}
	&__list + &__table {
		margin-top: 24px;
	}
}
