// В этом файле должны быть стили для БЭМ-блока field, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.field {
	&__inner {}
	&__label {
		font-weight: normal;
		font-size: 14px;
		line-height: 17px;
	}
	&__input {
		margin-top: 8px;
	}
	&__row {
		display: flex;
		flex-direction: column;
		@media (min-width: $screen-nexus) {
			flex-direction: row;
		}
		input + button {
			font-size: 14px;
			margin-top: 8px;
			padding: 8px 16px;
			@media (min-width: $screen-nexus) {
				margin-left: 8px;
			}
		}
	}
}
