// В этом файле должны быть стили для БЭМ-блока modal, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.modal {
	padding: 24px;
	box-shadow: var(--card-shadow);
	border-radius: 32px;
	width: calc(100% - 32px);
	@media (min-width: $screen-nexus) {
		max-width: 480px;
		padding: 40px;
	}
	&__header {
		display: flex;
		justify-content: space-between;
	}
	&__heading {
		color: var(--color-green);
		font-family: var(--font-accent);
		font-feature-settings: 'pnum' on, 'lnum' on;
		font-size: 24px;
		font-weight: bold;
		letter-spacing: 0.01em;
		line-height: 100%;
		margin: 0;
		@media (min-width: $screen-nexus) {
			font-size: 32px;
		}
	}
	&__close {
		cursor: pointer;
		width: 32px;
		height: 32px;
		margin-left: 16px;
		flex-shrink: 0;
	}
	&__form {
		margin-top: 32px;
	}
	&__agreement {
		margin-top: 48px;
	}
	&__body {
		margin-top: 24px;
	}
	&__text {
		font-size: 16px;
		font-weight: normal;
		line-height: 150%;
		margin: 16px 0 0 0;
	}
	&__list {
		display: grid;
		grid-gap: 16px;
	}
	&__promo {
		align-items: center;
		background: #eff1f4;
		border-radius: 16px;
		display: flex;
		flex-direction: column;
		font-size: 16px;
		justify-content: space-between;
		line-height: 1;
		margin-top: 8px;
		padding: 12px 12px 12px 24px;
		@media (min-width: $screen-nexus) {
			flex-direction: row;
		}
		span {
			font-size: 20px;
			font-weight: 700;
			line-height: 120%;
		}
	}
	&__copy {
		align-items: center;
		background: #ffffff;
		border: 2px solid transparent;
		border-radius: 8px;
		cursor: pointer;
		display: flex;
		font-size: 16px;
		line-height: 100%;
		margin-top: 16px;
		padding: 6px 10px;
		transition: border .25s ease;
		user-select: none;
		&:hover {
			border: 2px solid var(--color-green);
		}
		@media (min-width: $screen-nexus) {
			margin: 0;
		}
	}
	&__icon {
		height: 24px;
		margin-right: 8px;
		width: 24px;
	}
	&__small {
		font-size: 14px;
		line-height: 150%;
		margin: 0 auto;
		max-width: 336px;
		text-align: center;
		@media (min-width: $screen-sm) {
			font-size: 16px;
		}
		span {
			font-weight: 700;
		}
	}
	&__share {
		align-items: center;
		display: flex;
		justify-content: center;
		margin: 0 0 24px;
	}
	&__share-title {
		font-size: 16px;
		line-height: 1;
		margin-right: 16px;
	}
	&__share-row {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 8px;
	}
	&__share-item {
		transition: opacity .25s ease;
		&:hover {
			opacity: .75;
		}
	}
	&__share-icon {
		height: 48px;
		width: 48px;
	}
}
.hystmodal__shadow {
	background: rgba(0, 0, 0, .25);
}
