.notifier-container {
    border-radius: 50%;
	bottom: 16px;
	box-sizing: border-box;
	right: 16px;
	position: fixed;
	width: 280px;
	z-index: 999;
}
.notifier-container * {
	box-sizing: border-box;
}

.notifier {
    align-items: center;
	background: var(--body-color);
	border-radius: 24px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
	height: 48px;
	margin-top: 4px;
	opacity: 0;
	padding: 16px 16px 16px 20px;
	position: relative;
	transform: translateX(100%);
	transition: all .25s cubic-bezier(0.5, -0.5, 0.3, 1.4), opacity .25s ease;
	width: 100%;
}
.notifier.shown {
	opacity: 1;
	transform: translateX(0);
}
.notifier.shown:hover {
	opacity: 1;
}
.notifier-close {
	background: transparent;
	border-radius: 50%;
	border: none;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	font-weight: 700;
	height: 16px;
    line-height: 16px;
	opacity: .75;
	outline: none;
	padding: 0;
	text-align: center;
	text-decoration: none;
	transition: .25s ease;
	width: 16px;
}
.notifier-close:hover,
.notifier-close:focus {
	opacity: 1;
	color: #000;
	background: #fff;
}
.notifier-img {
    display: none;
}
.notifier-title {
    display: none;
}
.notifier-body {
    color: #fff;
    font-size: 16px;
}